.landingherointerest__wrapper {
  width: 250px;
  height: 250px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-flow: row nowrap;
  position: relative;
}

.landingherointerest__wrapper .landingherointerest__interest {
  width: 150px;
  height: 150px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-flow: row nowrap;
  background: #262223;
  border-radius: 50%;
  box-shadow: 0px 0px 12px rgba(242, 129, 29, 0.2);
  z-index: 100;
}

.landingherointerest__wrapper .landingherointerest__interest h2 {
  color: #f2f2f2;
  font-size: 1.5em;
  font-style: normal;
  margin: 0;
}

.landingherointerest__wrapper .landingherointerest__animationcontainerbox {
  width: 250px;
  height: 250px;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
}

.landingherointerest__wrapper .landingherointerest__animationcontainerbox span {
  position: absolute;
  box-sizing: border-box;
  border: none;
  background: rgba(242, 129, 29, 0.25);
  border-radius: 50%;
  animation: animate 5s linear infinite;
  animation-delay: calc(-0.5s * var(--i));
}

@keyframes animate {
  0% {
    width: 0px;
    height: 0px;
  }
  50% {
    opacity: 1;
  }
  100% {
    width: 250px;
    height: 250px;
    opacity: 0;
  }
}
